import React from 'react';
import './header.css';
import Logo from "./Logo";
import Nav from "./Nav";
import Menu from "./Menu";

function Header() {
    return (
        <header
            id="header"
            className="header fixed-top d-flex align-items-lg-center">
            {/* {logo} */}
            <Logo />
            {/* {menu} */}
            <Menu />
            {/* {nav} */}
            <Nav/>
        </header>
    );
}

export default Header;