// Table.jsx
import React from 'react';
import './table.css';

function Table() {
    return (
        <table className="table-container table-borderless datatable">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col">( )</th>
                    <th scope="col">( )</th>
                </tr>
            </thead>
            <tbody>
            {/*  
                {items && items.length > 0 &&
                 items.map(item => (
                     <tr key={item._id}>
                         <th scope="row">
                             <a href="#">{item.CUR_UNIT}</a>
                         </th>
                         <td>
                             <a href="#" className="text-primary">
                                {item.CUR_NM}
                            </a>
                         </td>
                         <td>{item.DEAL_BAS_R}</td>
                         <td>{item.TTB}</td>
                         <td>{item.TTS}</td>
                     </tr>
                 ))}
                 */}
            <tr>
                <td> </td>
                <td>USD</td>
                <td>...</td>
                <td>...</td>
                <td>...</td>
            </tr>
            <tr>
                <td> </td>
                <td>JPY</td>
                <td>...</td>
                <td>...</td>
                <td>...</td>
            </tr>
            <tr>
                <td> </td>
                <td>EUR</td>
                <td>...</td>
                <td>...</td>
                <td>...</td>
            </tr>
            <tr>
                <td> </td>
                <td>CNY</td>
                <td>...</td>
                <td>...</td>
                <td>...</td>
            </tr>
            </tbody>
        </table>
    );
}

export default Table;
